@media only screen and (min-width: 150px) and (max-width: 820px) {  
  /*Mobile View */
  .banner{
    width: 100%;
  }

  .bannerInfo{
    padding-left: 8%;
  }

  .geekly{
    font-size: 30px;
  }

  .geekly>.Typewriter{
    font-size: 25px;
  }

  .banner{
    /* background-size: cover;   */
    /* width: 100%; */
    /* background-repeat: no-repeat; */
    /* margin: auto; */
    background-image: url('../../img/bannerResp.jpg');

  }
}
/* @media (max-width: 1024px) {

} */

@media only screen and (min-width: 760px) and (max-width: 1024px) {  
  /*Mobile View */
  .banner{
    width: 100%;
  }

  .bannerInfo{
    padding-left: 20%;
  }

  .geekly{
    font-size: 50px;
  }

  .geekly>.Typewriter{
    font-size: 45px;
  }
  .balisebody {
    display: block;
    font-size: 35px;
    color: #ca40ba;
    font-weight: 800;
    text-shadow: none;
    /* margin: 3px 10px */
  }
  .baliseh1{
    display: block;
    font-size: 31px;
    color: #ca40ba;
    font-weight: 800;
    text-shadow: none;
    margin: 30px 40px
  }



  .banner{
    /* background-size: cover;   */
    /* width: 100%; */
    /* background-repeat: no-repeat; */
    /* margin: auto; */
    background-image: url('../../img/bannerResp.jpg');
    width: 100%;
  }}